import React from 'react';
import { Link } from "gatsby"

import './styles.scss';

import f_icon_color from '../../Assets/facebook_icon_color.png';
import f_icon_black from '../../Assets/facebook_icon_black.png';
import insta_icon_color from '../../Assets/instagram_icon_color.png';
import insta_icon_black from '../../Assets/instagram_icon_black.png';

const Social = ({ scroll }) => {
    return (
        <>
            <div className="social-icons nav-item">
                <p className="text-center nav-social-text">Follow <span className={scroll ? "span-scroll" : "span-no-scroll"}>nowCaptain</span></p>
                <img className="social-icon" src={scroll ? f_icon_black : f_icon_color} alt="Facebook logo" />
                <img className="social-icon" src={scroll ? insta_icon_black : insta_icon_color} alt="Instagram logo" />
            </div>
        </>
    )
}

export default Social;